/* eslint-disable no-unused-vars */
/* eslint-disable no-shadow */
export enum API_URLS {
  LOGIN = 'https://rasteedi.herokuapp.com/auth/login',
  LOGOUT = 'https://rasteedi.herokuapp.com/auth/logout',
  REFRESH = 'https://rasteedi.herokuapp.com/auth/refresh',
  ADD_NEW_INVENTORY_ENTRY = 'https://rasteedi.herokuapp.com/items/inventory',
  ADD_NEW_SOLD_ENTRY = 'https://rasteedi.herokuapp.com/items/sold_inventory',
  SEARCH_ITEM = 'https://rasteedi.herokuapp.com/items/inventory',
  UPDATE_ITEM = 'https://rasteedi.herokuapp.com/items/inventory',
}

export class ApiRequest<RequestBody = any, Response = any> {
  private url: string;

  private method: 'get' | 'post' | 'search' | 'PATCH';

  private headers: Headers;

  private reqBody: string;

  constructor(url: string) {
    this.url = url;
    this.method = 'get';
    this.reqBody = '';
    this.headers = new Headers();
    this.headers.set('Content-Type', 'application/json');
    if (
      this.url !== API_URLS.LOGIN &&
      this.url !== API_URLS.LOGOUT &&
      this.url !== API_URLS.REFRESH
    ) {
      this.headers.set(
        'Authorization',
        `Bearer ${localStorage.getItem('jwt')}`
      );
    }
  }

  public post(): this {
    this.method = 'post';
    return this;
  }

  public patch(): this {
    this.method = 'PATCH';
    return this;
  }

  public get(): this {
    this.method = 'get';
    return this;
  }

  public search(): this {
    this.method = 'search';
    return this;
  }

  public body(value: RequestBody): this {
    this.reqBody = JSON.stringify(value);
    return this;
  }

  public async execute(returnValue = true): Promise<Response | undefined> {
    const data = await fetch(this.url, {
      method: this.method,
      headers: this.headers,
      body: this.method !== 'get' ? this.reqBody : null,
    });
    if (data.ok) {
      return returnValue ? ((await data.json()) as Response) : undefined;
    }
    if (data.status === 401) {
      const refresh = await fetch(API_URLS.REFRESH, {
        method: 'post',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          refresh_token: localStorage.getItem('refresh_token'),
        }),
      });
      if (refresh.ok) {
        const jsonData = await refresh.json();
        localStorage.setItem('jwt', jsonData.data.access_token);
        this.headers.set(
          'Authorization',
          `Bearer ${localStorage.getItem('jwt')}`
        );
        return this.execute();
      }
      await new ApiRequest(API_URLS.LOGOUT)
        .post()
        .body({ refresh_token: localStorage.getItem('refresh_token') })
        .execute(false);
      localStorage.clear();
      window.location.reload();
    }
    throw new Error(`Request failed: ${this.url}`);
  }
}
