import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import Home from '../views/Home.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    beforeEnter: (_to, _from, next) => {
      if (!localStorage.getItem('jwt')) {
        next('/login');
      }
      next();
    },
    redirect: { name: 'Lager' },
    children: [
      {
        path: '/lager',
        name: 'Lager',
        component: () =>
          import(/* webpackChunkname: "views" */ '../views/Lager.vue'),
      },
      {
        path: '/product/:productCode',
        name: 'Item',
        component: () =>
          import(/* webpackChunkname: "views" */ '../views/Item.vue'),
      },
      {
        path: 'verkauf',
        name: 'Verkauf',
        component: () =>
          import(/* webpackChunkname: "views" */ '../views/Verkauf.vue'),
      },
      {
        path: 'wareneingang',
        name: 'Wareneingang',
        component: () =>
          import(/* webpackChunkname: "views" */ '../views/Wareneingang.vue'),
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: () =>
      import(/* webpackChunkName: "login" */ '../views/Login.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
