  <div class="relative min-h-screen md:flex">
    <!-- mobile menu -->
    <div class="bg-green-800 text-blue-100 flex justify-between md:hidden">
      <!-- logo -->
      <a
        @click="routeTo('Home')"
        class="block p-4 text-white font-bold cursor-pointer"
        >Rasteedi</a
      >
      <button
        @click="toggleMenu()"
        class="p-4 focus:outline-none focus:bg-green-500"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </button>
    </div>

    <!-- sidebar -->
    <div
      :class="{ '-translate-x-full': menuClosed }"
      class="
        bg-green-900
        text-green-100
        w-64
        space-y-4
        pl-4
        py-8
        absolute
        inset-y-0
        left-0
        transform
        md:relative md:translate-x-0
        transition
        duration-200
        ease-in-out
        z-50
      "
    >
      <!-- Logo -->
      <a
        @click="routeTo('Home')"
        class="text-white flex items-center space-x-2 px-4 cursor-pointer"
      >
        <img
          src="./../assets/rt.png"
          alt="Logo"
          class="object-contain w-12 rounded-full"
        />
        <div class="text-white text-2xl font-bold">Rasteedi</div>
      </a>
      <!-- nav -->
      <nav>
        <a
          @click="routeTo('Home')"
          :class="{ 'bg-green-700': $route.name == 'Lager' }"
          class="
            cursor-pointer
            font-bold
            block
            py-5
            px-4
            mr-0
            hover:bg-green-700
          "
          >Lager</a
        >
        <a
          @click="routeTo('Verkauf')"
          :class="{ 'bg-green-700': $route.name == 'Verkauf' }"
          class="
            cursor-pointer
            font-bold
            block
            py-5
            px-4
            mr-0
            hover:bg-green-700
          "
          >Verkauf</a
        >
        <a
          @click="routeTo('Wareneingang')"
          :class="{ 'bg-green-700': $route.name == 'Wareneingang' }"
          class="
            cursor-pointer
            font-bold
            block
            py-5
            px-4
            mr-0
            hover:bg-green-700
          "
          >Wareneingang</a
        >
        <a
          href=""
          class="
            hidden
            cursor-pointer
            font-bold
            py-5
            px-4
            mr-0
            hover:bg-green-700
          "
          >Verkaufshistorie</a
        >
        <a
          @click="logout()"
          class="
            cursor-pointer
            font-bold
            py-3
            px-2
            bg-red-500
            text-center
            flex
            justify-between
            mt-40
          "
          >Logout<svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1"
            /></svg
        ></a>
      </nav>
    </div>
    <!-- content -->
    <div class="flex-1">
      <router-view></router-view>
    </div>
  </div>
</template>

<script lang="ts">
import { ApiRequest, API_URLS } from '@/api/request.api';
import { defineComponent, ref } from 'vue';
import { useRouter } from 'vue-router';

export default defineComponent({
  name: 'Home',
  setup() {
    const jwt = localStorage.getItem('jwt');
    const router = useRouter();
    const menuClosed = ref(true);

    const toggleMenu = () => {
      menuClosed.value = !menuClosed.value;
    };

    const routeTo = (name: string) => {
      toggleMenu();
      router.push({ name });
    };

    const logout = async () => {
      await new ApiRequest(API_URLS.LOGOUT)
        .post()
        .body({ refresh_token: localStorage.getItem('refresh_token') })
        .execute(false);
      localStorage.clear();
      router.push({ name: 'Login' });
    };

    return { jwt, routeTo, toggleMenu, menuClosed, logout };
  },
  beforeRouteUpdate(to, from, next) {
    if (to.name !== from.name) {
      next();
    }
  },
});
</script>
